
h1 {
  font-family: 'Muli', serif;
  color: #2eb872;
}
h2 {
  font-family: 'Muli', serif;
  font-weight: normal;
}
h3 {
  font-family: 'Muli', serif;
  font-weight: normal;
}
body {
  margin-top: 2.828em;
  margin-bottom: 2.828em;
  font-family: 'Open Sans', sans-serif;
  color: #4d4d4c;
  background-color: #feffe4;
}
a {
  color: #fa4659;
  text-decoration: none;
}
a:hover {
  text-decoration: underline;
}
pre {
  margin-bottom: 0;
}
code {
  font-family: 'Roboto Mono', monospace;
}
code:not(.hljs) {
  padding-right: 0.5em;
  padding-left: 0.5em;
  white-space: normal;
  background-color: white;
  border-radius: 4px;
}
ol {
  margin-bottom: 20.8px;
  margin-bottom: 20.8px;
  margin-bottom: 1.3rem;
}
ol > li + li {
  margin-top: 20.8px;
  margin-top: 20.8px;
  margin-top: 1.3rem;
}
dl {
  clear: both;
}
dt {
  float: left;
}
dd {
  padding-bottom: 20.8px;
  padding-bottom: 20.8px;
  padding-bottom: 1.3rem;
  padding-left: 130px;
  margin-bottom: 20.8px;
  margin-bottom: 20.8px;
  margin-bottom: 1.3rem;
  border-bottom: 2px solid #a3de83;
}
dl dd:last-child {
  padding-bottom: 0;
  margin-bottom: 0;
  border-bottom: 0;
}
hr {
  height: 2px;
  margin-top: 62.4px;
  margin-top: 62.4px;
  margin-top: 3.9rem;
  color: #a3de83;
  background-color: #a3de83;
  border: 0;
}



