
.code-example {
  margin-bottom: 20.8px;
  margin-bottom: 20.8px;
  margin-bottom: 1.3rem;
  background-color: white;
  border: 2px solid #a3de83;
  border-radius: 4px;
}
.code-example-demo {
  padding-top: 0.5em;
  padding-bottom: 0.5em;
  border-bottom: 2px solid #a3de83;
}
.hljs {
  line-height: 1.45;
}
.code-example-demo .vfg-container,
.code-example-demo .vfg-grid-item {
  font-family: 'Roboto Mono', monospace;
  line-height: 2;
  color: white;
  text-align: center;
  background-color: #fa4659;
  background-clip: content-box;
}
.code-example-demo .vfg-grid-item.large {
  line-height: 6;
}
.code-example-demo .vfg-container {
  color: #4d4d4c;
  background-color: rgba(250, 70, 89, .1);
  background-clip: border-box;
}
.code-example-demo .vfg-grid-item .vfg-grid-item {
  background-color: rgb(114, 3, 14);
}
.code-example-demo .vfg-grid-pair > .vfg-grid-item {
  text-align: left;
}
.code-example-demo .vfg-grid-pair > .vfg-grid-item + .vfg-grid-item {
  text-align: right;
}



