
html {
  box-sizing: border-box;
  overflow-y: scroll;
}
body {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  font-size: 1em;
  line-height: 1.45;
}
*, *::before, *::after {
  box-sizing: inherit;
}
img {
  box-sizing: content-box;
  vertical-align: middle;
}
p, dl, dd, hr, blockquote, pre,
table, th, td,
form, fieldset, caption, legend {
  padding: 0;
  margin: 0;
}
ul, ol {
  margin-top: 0;
}
