}
ul ul,
ol ol,
ul ol,
ol ul {
  margin-top: 0;
  margin-bottom: 0;
}
strong, em, small, code, hr {
  line-height: 1;
}
h1, h2, h3, h4, h5, h6 {
  margin-top: 1.414em;
  margin-bottom: 0.5em;
  line-height: 1.2;
}
h1 {
  margin-top: 0;
  font-size: 3.15733em;
}
h2 {
  font-size: 2.36859em;
}
h3 {
  font-size: 1.77689em;
}
h4 {
  font-size: 1.333em;
}
strong, em, small, code, hr {
  line-height: 1;
}
small {
  font-size: 0.75019em;
}
sup, sub {
  position: relative;
  vertical-align: baseline;
}
sup {
  top: -0.4em;
}
sub {
  bottom: -0.25em;
}
code {
  font-family: monospace;
  white-space: nowrap;
}
pre code {
  white-space: pre-wrap;
}
table {
  width: 100%;
  border-collapse: collapse;
}
thead {
  text-align: left;
}



